<template>
  <section id="aidou-options-page">
    <h1>配置面板</h1>
    <ul class="config-panel">
      <li class="config-item">
        <span class="config-name">图床服务：</span>
        <div class="config-content">
          <label>
            <span>微博图床</span>
            <input type="radio" :value="PIC_BED.WEIBO" v-model="config.picBed">
          </label>
          <label>
            <span>SM 图床</span>
            <input type="radio" :value="PIC_BED.SM" v-model="config.picBed">
          </label>
        </div>
      </li>
      <li class="config-item">
        <div class="config-content">
          <label>
            <span>显示图链接面板：</span>
            <input type="checkbox" v-model="config.showFullLinks">
          </label>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
import cstore from '@/common/cstore'

const PIC_BED = {
  SM: 'sm',
  WEIBO: 'weibo'
}

const DEFAULT = {
  showFullLinks: false,
  picBed: PIC_BED.SM
}

export default {
  data () {
    this.PIC_BED = PIC_BED
    return {
      config: Object.assign({}, DEFAULT)
    }
  },

  watch: {
    config: {
      deep: true,
      handler: 'saveConfig'
    }
  },

  created () {
    cstore.get(DEFAULT).then(config => {
      this.config = config
    })
  },

  methods: {
    saveConfig () {
      cstore.set(this.config)
    }
  }
}
</script>

<style lang="scss">
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html,
body {
  width: 100%;
  height: 100%;
  padding: 20px;
  font-size: 12px;
}

#aidou-options-page {
  width: 600px;
  height: 100%;
  padding: 10px;
  border: 1px solid #eee;
  border-radius: 4px;
  margin: 0 auto;
  color: #3e5165;
  font-weight: 200;

  .config-panel {
    list-style: none;
  }

  .config-item {
    padding: 20px 0;
    border-bottom: 1px dotted #eee;
  }

  .config-name {
    display: block;
    margin: 10px 0;
  }

  .config-content {
    margin-right: 10px;

    label {
      margin-right: 10px;

      span {
        margin-right: 6px;
        color: #929aa3;
      }
    }
  }
}
</style>




